import {useDispatch} from "react-redux";
import {setActiveSlide} from "../../../store/reducers/Swiper";

export const Screen3 = () => {
  const dispatch = useDispatch();
  return (
    <div >
      <button onClick={() => dispatch(setActiveSlide(1))}>Вернуться на 2 страницу</button>
      <button onClick={() => dispatch(setActiveSlide(3))}>Перейти на 4 страницу</button>
      <p>screen 3</p>
    </div>
  )
}